@import "fonts";
@import "variables";
@import "vendor";
@import "browser";

$mobile_menu_width: 690px; // 590px

/* theme colours */
$accent: #fff ;

/* theme images */
$heroImageRatio: 62.5%; // 62.5%;

$heroImageURL: '/logos/grace-killen-floral-artist-lineart-logo';
$heroImageExt: 'gif';

$logoImageURL: '/logos/grace-killen';
$logoImageExt: 'gif';

$galleryImageURL: 'https://thefloralartist.blob.core.windows.net/gallery/';

$aspectRatio: 75%; // 3/4 h/w 1440/1080

* {
  font-kerning: normal;
  font-smooth: always;
  font-optical-sizing: auto;
  font-variant-ligatures: common-ligatures;
}

h2 {
  font-variant-caps: small-caps;
  font-weight: normal;
}

h2, p {
  letter-spacing: 0.02em;
  text-rendering: optimizeLegibility; // geometricPrecision
  // text-shadow: rgba(0,0,0,.2) 0 0 1px;
}

a {
  color: black;
}

input, textarea {
  letter-spacing: 0.2em;
}

body {
  background: $background;
  font-family: $font_body;
  margin: 0 !important;
  padding: 0;
  min-width: $min_width;
  overflow: overlay;
  // visibility: visible;
  overflow-x: hidden;
}

#content {
  margin-bottom: 2em;
}

.profile {
  background-image: url('/images/grace-wedding-bouquet-painting-ireland-1.jpg');
  background-size: contain; // cover
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  padding-bottom: 100%;
}

footer {
  // position: fixed;
  // bottom: 0;
  background: white;
  width: 100%;
  text-align: center;
  div {
    padding: 1em;
  }
}

nav {
  height: $button_height;
  // height: calc(#{$nav_height} + 2 * #{$gutter});
  background: white;
  color: black;

  // position: fixed;
  // width: 100%;
  // z-index: 1;

  ul {
    // width: 100%;
    height: $button_height;
    list-style-type: none;
    margin: 0 auto; // center
    padding: 0;

    li {
      float: left;
      // margin-right: 1em;
    }
  }

  a {
    display: block;
    // box-sizing: border-box;
    color: black;
    text-decoration: none;
    font-weight: bold;
    font-size: 18px;
    height: $button_height;
    line-height: $button_height;
    padding: 0 1em;
  }

  .home {
    background-image: url('#{$logoImageURL}.#{$logoImageExt}');
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: 136px 14px;
    width: 136px;
    height: $button_height;
    // float: left;
    // margin-right: 1em;
  }

  #toggleMenu {
    position: absolute;
    top: 0;
    right: 0;
    height: $button_height;
    width: $button_height;
    background: white;
    border: 0;
    outline: none;
    cursor: pointer;
  }
}

h1, .display {
  font-family: $font_display;
  text-align: center;
}

.extract {
  text-align: center;
}

.display > a {
  text-decoration: none;
}

.posts li {
  list-style-type: none;
}

P {
  line-height: 200%;
}

@keyframes fadeIn {
  0% {opacity:0;filter: blur(40px);}
  100% {opacity:1;filter:none;}
}

.grid {
  display: grid;
  grid-gap: 1px 1px;
  grid-template-columns: auto auto auto;
}

.gallery {
  margin: 1px;
}

.gallery a {
  display: block;
  position: relative;
  width: 100%;
  // padding-bottom: $aspectRatio;
  // background-color: green;
  border: 2px solid white;
  box-sizing: border-box;
  overflow: hidden;
  padding: 0;
}

.gallery img, .post img {
  // position: absolute;
  // top: 0;
  // left: 0;
  // bottom: 0;
  // right: 0;
  display: block;
  width: 100%;
  min-height: 460px; 
  // padding-bottom: $aspectRatio;
  // background-color: red;
  object-fit: cover;
  box-sizing: border-box;
}

.gallery a > img:nth-child(2n) {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  animation: fadeIn ease 1s;
  animation-fill-mode: forwards;
  // opacity: 0.1;
  // filter: blur(40px);
}

// .gallery img {
//   object-fit: cover;
//   width: 100%;
//   min-height: 400px;
// }

.hero {
  color: black;
  background-color: white;
  background-image: url('#{$heroImageURL}.#{$heroImageExt}');
  // background-size: 520px 325px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 0;

  
  // height: calc(100vh - #{$nav_height});
  // min-height: $min_height;
  position: relative;
  padding-bottom: $heroImageRatio; // 56.25%
  
  .overlay {
    position: absolute;
    top: 0;
    display: block;
    content: '';
    height: 100%;
    width: 100%;
    background-image: linear-gradient(to right, rgba(0,0,0,0.7), rgba(0,0,0,0));
  }

  .caption {
    position: absolute;
    padding: 2em;
    width: $min_width;

    h1 {
      margin: 0;
      font-family: $font_display;
    }
  }  
}

// top, right, bottom, left
@keyframes one {
  from {
    clip: rect(0px, 284px, 0px, 0px);
    filter: brightness(400%) blur(40px);
    opacity: 0;
  }
  to {
    clip: rect(0px, 284px, 400px, 0px);
    filter: none;
    opacity: 1;
  }
}

@keyframes two {
  from {
    clip: rect(512px, 512px, 512px, 0px);
    filter: blur(40px);
    opacity: 0;
  }
  to {
    clip: rect(0px, 512px, 512px, 0px);
    filter: none;
    opacity: 1;
  }
}

.frame {
  position: relative;
  max-width: 460px;
  margin: 0 auto;
  height: 612px;
  background: white;
  box-sizing: border-box;
  // border: 1px solid gray;

  .one {
    animation-name: none;
    opacity: 0;
  }
  .two {
    animation-name: none;
    opacity: 0;
  }
}

.frame.animate {
  .one {
    position: absolute;
    width: 284px;
    height: 398px;
    background: url('/images/bridal_bouquet.jpg');
    background-repeat: no-repeat;
    right: 0;
    top: 0px;
    animation-name: one;
    animation-duration: 1s;
    animation-delay: 0.1s;
    animation-fill-mode: forwards;
  }
  
  .two {
    position: absolute;
    top: 290px;
    width: 320px;
    height: 320px;
    background: url('/images/bridal_bouquet_painting.jpg'); // '#{$galleryImageURL}/bridal-bouquet.jpeg'
    background-repeat: no-repeat;
    background-size: contain;
    left: 0px;
    animation-name: two;
    animation-duration: 1s;
    animation-delay: 1.1s;
    animation-fill-mode: forwards;
  }
}


.intro {
  text-align: center;
  font-size: 32px;
  span {
    display: block;
  }
}

.featureImage {
  width: 100%;
  height: 300px;
  background: grey;
}

input[type=text], textarea {
  width: 100%;
  box-sizing: border-box;
  padding: 1em;
  margin: 1em 0;
  font-family: $font_body;
}
input[type=submit] {
  padding: 1em;
  background: black;
  color: white;
  text-transform: uppercase;
  font-family: $font_body;
  font-weight: bolder;
}

ul.tags, ul.categories {
  padding-inline-start: 0;
  li {
    list-style-type: none;
    float: left;
    margin-right: 5px;
    margin-bottom: 5px;
    a {
      display: block;
      background-color: $secondary;
      // color: white;
      border-radius: 5px;
      padding: 5px;
      text-decoration: none;
    }
  }
}

ul.categories {
  li a {
    background-color: black;
    color: white;
  }
}

.posts {
  .display {
    text-align: left;
  }
}
img.posts {
  width: 100%;
}

@media only screen and (max-width: $mobile_menu_width) {
  nav ul li.menu { display: none; }
}

@media only screen and (min-width: $mobile_menu_width) {
  nav #toggleMenu { display: none; }
}

@media only screen {
  body.modal {
    height: 100vh;
    overflow-y: hidden;
    position: fixed;

    nav {
      background: red;

      #toggleMenu {
        z-index: 2;
        display: block;
        background: white;
      }
  
      ul {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        display: block;
        background: white;
        color: black;
        width: 100vw;
        height: 100vh;
  
        li {
          float: none;
          display: block;
          text-align: center;
          margin: 0;

          a {
            padding: 0;
          }
        }
  
        .home {
          width: 100%;
          margin: 0;
          // margin-top: $button_height;
        }
      }
    }
  }
}

// hero
@media only screen and (min-width: 520px) {
  .hero {
    background-image: url('#{$heroImageURL}@2x.#{$heroImageExt}');
  }
}
@media only screen and (min-width: 1040px) {
  .hero {
    background-image: url('#{$heroImageURL}@3x.#{$heroImageExt}');
  }
}
@media only screen and (min-width: 2080px) {
  .hero {
    background-image: url('#{$heroImageURL}@4x.#{$heroImageExt}');
  }
}

// @2x
@media only screen and (-o-min-device-pixel-ratio: 5/4),
       only screen and (-webkit-min-device-pixel-ratio: 1.25),
       only screen and (min-device-pixel-ratio: 1.25),
       only screen and (min-resolution: 1.25dppx) {
  nav .home {
    background-image: url('#{$logoImageURL}@2x.#{$logoImageExt}');
  }
  .hero {
    background-image: url('#{$heroImageURL}@2x.#{$heroImageExt}');
  }
}

// @3x     
@media only screen and (-o-min-device-pixel-ratio: 5/4),
       only screen and (-webkit-min-device-pixel-ratio: 1.25),
       only screen and (min-device-pixel-ratio: 1.25),
       only screen and (min-resolution: 1.25dppx) {
  nav .home {
    background-image: url('#{$logoImageURL}@3x.#{$logoImageExt}');
  }
  .hero {
    background-image: url('#{$heroImageURL}@3x.#{$heroImageExt}');
  }
}

// @4x
@media only screen and (-o-min-device-pixel-ratio: 5/4),
       only screen and (-webkit-min-device-pixel-ratio: 1.25),
       only screen and (min-device-pixel-ratio: 1.25),
       only screen and (min-resolution: 1.25dppx) {
  nav .home {
    background-image: url('#{$logoImageURL}@4x.#{$logoImageExt}');
  }
  .hero {
    background-image: url('#{$heroImageURL}@4x.#{$heroImageExt}');
  }
}